/**
 * Logitech color palette
 */

// Bright colors

$azzuro: rgb(000, 191, 255);
$azzuro-light: rgb(180, 236, 255);
$azzuro-ultralight: lighten($azzuro-light, 13%);

.azzuro-background {
    background: $azzuro;
}

.azzuro-color {
    color: $azzuro;
}

$neo: rgb(255, 104, 097);

.neo-color {
    color: $neo;
}

.neo-background {
    background: $neo;
}

$elettrico: rgb(220, 253, 000);

.elettrico-color {
    color: $elettrico;
}

.elettrico-background {
    background: $elettrico;
}

$ciano: rgb(000, 234, 208);

.ciano-color {
    color: $ciano;
}

.ciano-background {
    background: $ciano;
}

$lila: rgb(129, 078, 250);

.lila-color {
    color: $lila;
}

.lila-background {
    background: $lila;
}

$kirsche: rgb(255, 041, 071);

.kirsche-color {
    color: $kirsche;
}

.kirsche-background {
    background: $kirsche;
}

// Neutrals

$kohle: rgb(46, 49, 50);

.kohle-color {
    color: $kohle;
}

.kohle-background {
    background: $kohle;
}

$acier: rgb(195, 198, 200);

.acier-color {
    color:$acier;
}

.acier-background {
    background: $acier;
}

// Additional
$orpheus: rgb(66,250, 133);
$helios: rgb(255,127,44);
$bavert: rgb(120,195,116);
$steel: rgb(139,140,144);
$charbon: lighten($kohle, 10%);

