/***********************
 * App-specific Styles *
 ***********************/

///////////////////////////////////////////////////////////////////////////////
// Colors

@import "./logi-colors.scss";

///////////////////////////////////////////////////////////////////////////////
// Fonts

@font-face {
    font-family: 'BrownPro';
    src: url('./fonts/BrownPro-Regular.ttf') format('truetype');
}

@font-face {
    font-family: 'BrownPro';
    font-weight: bold;
    src: url('./fonts/BrownPro-Bold.ttf') format('truetype');
}

///////////////////////////////////////////////////////////////////////////////
// Bootstrap

// Import bootstrap grid layout

@import '../../node_modules/bootstrap/scss/bootstrap-grid.scss';

// Media breakpoints (screen fit)

@include media-breakpoint-up(xs) {
  html {
    font-size: 0.55rem;
  }
}

@include media-breakpoint-up(sm) {
  html {
    font-size: 0.7rem;
  }
}

@include media-breakpoint-up(md) {
  html {
    font-size: 0.8rem;
  }
}

@include media-breakpoint-up(lg) {
  html {
    font-size: 0.9rem;
  }
}

@include media-breakpoint-up(xl) {
  html {
    font-size: 1rem;
  }
}

// Buttons

.btn {
    border: none;
    border-radius: 0;
    text-transform: uppercase;
}

.btn-black {
    color: white;
    background: black;
    font-weight: normal;
}

.btn-kirsche {
    color: white;
    border-radius: 0;
    background: $kirsche;
    font-weight: normal;
}

.btn-kohle {
    color: white;
    border-radius: 0;
    background: $kohle;
    font-weight: normal;
}

// Grid system

.container-fluid {
  padding-left: 0px;
  padding-right: 0px;
}

.row {
  margin-left: 0px;
  margin-right: 0px;
}

// Modal dialogs

.modal-container {
    background-color: $acier;
    color: white;
}

.modal-wrapper {
    height: auto;
}

///////////////////////////////////////////////////////////////////////////////
// Standard components

html, body {
  height: 100%;
}

body {
  font-family: 'BrownPro';
  /* overflow: hidden; */
}

:focus {
  outline: none !important;
}

///////////////////////////////////////////////////////////////////////////////
// App-specific components

/* Modal dialogs */

.modal-container {
  background-color: $acier;
  color: white;
}

.modal-wrapper {
  height: auto;
}

/* App */

.app {
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
}
